<template>
    <div class="card mb-4 mt-4">
        <div class="card-header">
            #{{content.id}}
        </div>
        <div class="card-body">
            <p class="card-text">
                <template v-for="[fieldKey, fieldValue] in Object.entries(correspondenceOfFieldsAndRusLabels)" :key="fieldKey">
                
                    <strong>{{fieldValue.label}}</strong>: {{content[fieldKey]}} <br>
                
                </template>
            </p>
            <p class="card-text">
                <small class="text-muted">
                    Добавил: {{content['user.fullName']}}<br>
                    Дата создания: {{content.createdAt}}
                </small>
            </p>
        </div>
        <div class="card-footer text-muted">
            <button type="button" class="btn btn-primary" @click="$emit('id-has-been-selected', content.id)">Выбрать</button>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        content:Object,
        correspondenceOfFieldsAndRusLabels:Object
    },
}
</script>