<template>
<div class="shadow-sm bg-light p-5 rounded mt-3">
    <div v-if="!showSuccessAlert">
        
        <div class="mb-3" v-for="field in formFields" :key="field.label">
            <template v-if="field.element">
                
                {{field.label}}: {{field.value}}

                <component 
                    v-model="field.value"
                    :is="field.element" 
                    v-bind="field.elementProps"
                >
                </component>
               
            </template>
            <template v-else>
                <LabelInput :labelText="field.label" v-model="field.value"/>
            </template>
        </div>

        <button class="btn btn-primary" @click="create()">Создать</button>

    </div>
    <div v-else class="alert alert-success" role="alert">
        Добавлено.
        <button type="button" class="btn btn-primary" @click="$router.go()">Добавить еще</button>
    </div>
</div>
</template>

<script>
import Axios from '@/axios'
import LabelInput from '@/components/elements/LabelInput.vue'

const keyValueObjectFromFormFields = require('@/utility/keyValueObjectFromFormFields.js')

export default {
    components:{ LabelInput },
    props:{
        formFieldsProp: Object,
        postUrl: String
    },
    data(){
        return{
            formFields:{},
            showSuccessAlert:false,
        }
    },
    methods:{
        async create(){

            const { formFields } = this

            const dataForServer = keyValueObjectFromFormFields(formFields)

            await Axios.post(this.postUrl, dataForServer)
            
            this.showSuccessAlert = true

        }
    },
    created(){
        this.formFields = {...this.formFieldsProp}
    }
}
</script>