<template>
    <div>

        <button 
            type="button" 
            class="btn btn-primary" 
            @click="showList=!showList"
        >
            Выбрать из списка
        </button>

        <button 
            type="button" 
            class="btn btn-link" 
            @click="idHasBeenSelected('')"
            v-if="modelValue"
        >
            Сбросить
        </button>

        <div 
            v-if="showList"
            class="shadow-sm border border-4 rounded overflow-auto pl-4 pr-4 pt-1 mt-2" 
            style="max-height:600px;"
        >
            <ListWithSelect 
                :getUrl="getUrl"
                :correspondenceOfFieldsAndRusLabels="correspondenceOfFieldsAndRusLabels"
                @id-has-been-selected="idHasBeenSelected($event)"
            />
        </div>
    </div>
</template>

<script>
import ListWithSelect from '@/components/basic/ListWithSelect/List.vue'

export default {
    components:{
        ListWithSelect
    },
    props:{
        modelValue: String,
        getUrl: String,
        correspondenceOfFieldsAndRusLabels: Object
    },
    data(){
        return{
            showList: false
        }
    },
    methods:{
        idHasBeenSelected(id){
            this.showList = false
            this.$emit('update:modelValue', id)
        }
    }
}
</script>